import React, { Component } from "react";
import "./App.css";

// Componennts

import ChannelList from "./components/ChannelList";
import ChannelInfo from "./components/ChannelInfo";
import Header from "./components/Header";

class App extends Component {
  constructor() {
    super();
    this.state = {
      channels: [
        {
          name: "JWPlatform",
          url: "https://content.jwplatform.com/manifests/vM7nH0Kl.m3u8"
        },
        {
          name: "Yemen Shabbab",
          url:
            "http://stream.starmenacdn.com:8080/hls/yemenshabab.m3u8?v=1657905409"
        },
        {
          name: "Al-Shareyyah",
          url:
            "http://streamer12.elementssys.com:8080/SAUDI-Channels/AL-SHAREYYAH/index.m3u8"
        },
        {
          name: "Al-Saeedah",
          url:
            "http://178.132.5.224:8000/live/ag6677/8473993/3911.ts?"
        }
      ],
      current: null,
      showChannels: true,
      search: ""
    };
  }
  onChannelChange = channel => {
    this.setState({ current: channel });
    console.log(channel);
  };
  onChannelsToggle = status => {
    this.setState({ showChannels: status });
    console.log(this.state.showChannels);
  };
  onInputChange = e => {
    this.setState({ search: e.target.value });
  };
  render() {
    const { channels, search } = this.state;
    const filteredChannels = channels.filter(channel => {
      return channel.name.toLowerCase().includes(search.toLowerCase());
    });
    return (
      <div className="container">
        <Header
          onInputChange={this.onInputChange}
          status={this.state.showChannels}
          onChannelsToggle={this.onChannelsToggle}
        />
        <div className="container-content">
          <ChannelList
            status={this.state.showChannels}
            channels={filteredChannels}
            onChannelChange={this.onChannelChange}
          />
          <ChannelInfo channel={this.state.current} />
        </div>
      </div>
    );
  }
}

export default App;
