import React from "react";
import Scroll from "./Scroll";
import ChannelListItem from "./ChannelListItem";

const ChannelList = ({ onChannelChange, channels, status }) => {
  return status ? (
    <div className="channels-list mr-3 mt-1">
      <Scroll>
        <ul className="list-group">
          {channels ? (
            channels.map(channel => {
              return (
                <ChannelListItem
                  key={channel.name}
                  channel={channel}
                  onChannelChange={onChannelChange}
                />
              );
            })
          ) : (
            <h4>No Channels Provided to Parse</h4>
          )}
        </ul>
      </Scroll>
    </div>
  ) : null;
};

export default ChannelList;
